

























import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Capture extends Vue {

  private video: any = {};
  private canvas: any = {};
  private captures: any[] = new Array<any>();

  public mounted(): void {
    this.SetCamera();
  }

  private async SetCamera(): Promise<void> {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      console.info('Disabled MediaDevices');
      return;
    }

    this.video = this.$refs.video;
    const stream = await navigator.mediaDevices.getUserMedia({ video: true });
    this.video.srcObject = stream;
    this.video.play();
  }

  private capture() {
    this.canvas = this.$refs.canvas;
    this.canvas.getContext('2d').drawImage(this.video, 0, 0, 500, 500);
    this.captures.push(this.canvas.toDataURL('image/png'));
  }
}
